
.customer-bp-list-view .remark-input {
  width: 450px;
  display: block;
  margin: auto;
}
.customer-bp-list-view .set-remark-dialog {
  z-index: 2000;
  transform: none;
  display: none;
  min-width: auto;
}
.customer-bp-list-view .set-remark-dialog:after {
  content: '';
  display: block;
  position: absolute;
  top: -7px;
  z-index: 101;
  left: 482px;
  width: 10px;
  height: 10px;
  background: #fff;
  border-top: 1px solid rgba(0,0,0,0.1);
  border-right: 1px solid rgba(0,0,0,0.1);
  transform: rotate(-45deg);
}
.customer-bp-list-view .set-remark-dialog .form-line {
  margin-top: 0;
}
.customer-bp-list-view .set-remark-dialog .el-input {
  padding: 40px 30px 40px 30px;
}
.customer-bp-list-view .set-remark-dialog .el-dialog__body {
  padding: 0;
  margin: 0;
}
.customer-bp-list-view .set-remark-dialog .el-dialog__footer {
  padding: 0;
  padding-bottom: 40px;
}
.my_table .el-table__body-wrapper .cell {
  height: auto;
  line-height: 24px;
}
.my_table td,
.my_table tr {
  border: none !important;
}


