
.admin-message .message-detail-dialog .el-dialog__body {
  padding: 0 20px 20px;
}
.admin-message .message-detail-dialog .el-dialog--tiny {
  width: 520px;
  border-radius: 10px;
}
.admin-message .message-detail-dialog .el-dialog__header {
  padding: 15px 0;
  margin: 0 20px;
  border-bottom: 1px solid #ebe9e9;
}
.admin-message .message-detail-dialog .el-dialog__header .el-dialog__title {
  font-weight: normal;
  color: #000;
  font-size: 17px;
}
.admin-message .message-detail-dialog .el-dialog__header .el-dialog__close {
