  font-size: 13px;
  font-weight: normal;
}
.admin-message .message-detail-dialog .list .item {
  margin-top: 30px;
  overflow: hidden;
  color: #000;
}
.admin-message .message-detail-dialog .list .item .label {
  float: left;
  width: 130px;
  text-align: right;
}
.admin-message .message-detail-dialog .list .item .detail {
  float: left;
  width: 290px;
  margin-left: 15px;
}


