
.admin-list .el-table .cell {
  font-size: 13px;
  font-weight: normal;
  padding-right: 0;
  color: #000;
}
.admin-list .el-table th>.cell {
  font-size: 14px;
  color: #666;
  font-weight: bold;
}
.admin-list .row-click {
  cursor: pointer;
  margin-right: 10px;
  color: #20a0ff;
}
.admin-list .create-btn {
  padding: 7px 15px;
  border-color: #a5a5a5;
}
.admin-list .group-icon {
  position: relative;
}
.admin-list .group-icon:before {
  font-family: element-icons !important;
  content: "\E607";
  position: absolute;
  right: 10px;
  top: 50%;
  font-size: 12px;
  margin-top: -12px;
  color: #d3dce6;
  transform: rotate(180deg);
}
.admin-list2 .el-table .cell {
  cursor: pointer;
}


