
.fast-reply-dialog .el-dialog {
  width: 500px;
  border-radius: 5px;
}
.fast-reply-dialog .el-dialog .el-dialog__body {
  padding: 0;
}
.fast-reply-dialog .el-dialog .el-dialog__body .title {
  font-size: 18px;
  color: #000;
  padding-bottom: 20px;
  border-bottom: 1px solid #e7e7e7;
  margin: 0 20px 30px;
  font-weight: normal;
}
.fast-reply-dialog .el-dialog .el-dialog__body .title span {
  padding-left: 20px;
}
.fast-reply-dialog .el-dialog .el-dialog__body .content {
  padding: 0 10px 30px;
  margin: 0 20px;
  border-bottom: 1px solid #e7e7e7;
  position: relative;
}
.fast-reply-dialog .el-dialog .el-dialog__body .content .tips {
  position: absolute;
  right: 20px;
  bottom: 40px;
  color: #a5a5a5;
  font-size: 12px;
}
.fast-reply-dialog .el-dialog .dialog-footer2 {
  margin: 0 20px;
  padding: 20px;
  text-align: center;
}
.fast-reply-dialog .el-dialog .dialog-footer2 .el-button {
  width: 88px;
}
.fast-reply-dialog .el-dialog .dialog-footer2 .el-button+.el-button {
  margin-left: 30px;
}
.admin-list .el-table .cell {
  font-size: 13px;
  font-weight: normal;
  padding-right: 0;
  color: #000;
}
.admin-list .el-table th>.cell {
  font-size: 14px;
  color: #666;
  font-weight: bold;
}
.admin-list .row-click {
  cursor: pointer;
  margin-right: 10px;
  color: #20a0ff;
}
.admin-list .create-btn {
  padding: 7px 15px;
  border-color: #a5a5a5;
}
.admin-list .group-icon {
  position: relative;
}
.admin-list .group-icon:before {
  font-family: element-icons !important;
  content: "\E607";
  position: absolute;
  right: 10px;
  top: 50%;
  font-size: 12px;
  margin-top: -12px;
  color: #d3dce6;
  transform: rotate(180deg);
}
.admin-list2 .el-table .cell {
  cursor: pointer;
}


