
.admin-list .el-table .cell {
  font-size: 13px;
  font-weight: normal;
  padding-right: 0;
  color: #000;
}
.admin-list .el-table th>.cell {
  font-size: 14px;
  color: #666;
  font-weight: bold;
}
.admin-list .row-click {
  cursor: pointer;
  margin-right: 10px;
  color: #20a0ff;
}
.admin-list .create-btn {
  padding: 7px 15px;
  border-color: #a5a5a5;
}
.admin-list .group-icon {
  position: relative;
}
.admin-list .group-icon:before {
  font-family: element-icons !important;
  content: "\E607";
  position: absolute;
  right: 10px;
  top: 50%;
  font-size: 12px;
  margin-top: -12px;
  color: #d3dce6;
  transform: rotate(180deg);
}
.admin-list2 .el-table .cell {
  cursor: pointer;
}
.admin-group-box .editor-group-dialog .el-dialog--tiny {
  width: 520px;
  border-radius: 10px;
}
.admin-group-box .editor-group-dialog .el-dialog__body {
  padding: 0;
  margin: 0 20px;
}
.admin-group-box .editor-group-dialog .el-dialog__header {
  height: 50px;
  line-height: 50px;
  padding: 0;
  margin: 0 20px;
  position: relative;
  border-bottom: 1px solid #e7e7e7;
}
.admin-group-box .editor-group-dialog .el-dialog__header span {
  font-size: 18px;
  color: #000;
  font-weight: normal;
}
.admin-group-box .editor-group-dialog .el-dialog__header .el-dialog__headerbtn {
  position: absolute;
  right: 0;
  top: 0;
}
.admin-group-box .editor-group-dialog .el-form-item__label:before {
  content: "" !important;
}
.admin-group-box .editor-group-dialog .el-form-item__label {
  text-align: left;
  padding-right: 0;
  font-weight: normal;
}
.admin-group-box .editor-group-dialog .el-form-item {
  margin: 30px 50px 0;
}
.admin-group-box .editor-group-dialog .dialog-footer2 {
  margin-top: 30px;
  padding: 20px 0;
  text-align: center;
  border-top: 1px solid #e7e7e7;
}
.admin-group-box .editor-group-dialog .dialog-footer2 .el-button+.el-button {
  margin-left: 30px;
}
.delete-group-dialog {
  border-radius: 10px;
  position: relative;
}
.delete-group-dialog .el-message-box__header {
  padding: 40px 0 0 80px;
}
.delete-group-dialog .el-message-box__content {
  position: static;
}
.delete-group-dialog .el-message-box__status {
  font-size: 24px !important;
  top: 40px;
  left: 45px;
  transform: inherit;
}
.delete-group-dialog .el-message-box__message p {
  font-size: 14px;
  font-weight: normal;
}
.delete-group-dialog .el-message-box__close {
  display: none;
}
.delete-group-dialog .el-message-box__btns {
  padding: 0 40px 30px 0;
}
.delete-group-dialog .el-message-box__btns .el-button {
  width: 65px;
}
.delete-group-dialog .el-message-box__btns button:nth-child(2) {
  margin-left: 30px;
}
.delete-group-dialog .el-message-box__message {
  margin-left: 60px !important;
}
.delete-group-dialog .el-message-box__content {
  padding-right: 40px;
}


