
.login-rule-form {
  position: relative;
}
.login-rule-form .el-form-item {
  margin-bottom: 30px !important;
}
.login-rule-form .el-form-item__label:before {
  content: "" !important;
}
.login-rule-form .el-input__inner {
  padding: 0 10px;
  font-size: 12px;
}
.login-rule-form .el-form-item__label {
  text-align: left;
  width: 200px;
}
.login-rule-form .el-checkbox__label {
  font-size: 12px;
}
.login-rule-form .login-btn {
  width: 200px;
  margin-top: 30px;
}
.login-rule-form .form-error-tips {
  position: absolute;
  left: 0;
  top: 158px;
  font-size: 12px;
  color: #ff4949;
}


