
[v-cloak]{display:none
}
html{height:100%;font-family:Microsoft YaHei,SimSun,Hiragino Sans GB,Arial,sans-serif
}
body{height:100%
}
*{margin:0;padding:0;outline:none
}
i{font-style:normal
}
ul,ol{margin:0;list-style:none
}
textarea{resize:none !important
}
a{text-decoration:none
}
.right-border-none{border:none;outline:none
}
.padding-right0{padding-right:0
}
.padding-left0{padding-left:0
}
::-ms-clear{display:none
}
::-ms-reveal{display:none
}
.el-date-picker{z-index:3000 !important
}
.el-popover{border-radius:4px !important
}
.admin-layout .el-input__inner{border-color:#B8B8B8;color:#000
}
.admin-layout .el-table td,.admin-layout .el-table th.is-leaf{border-bottom:none
}
.admin-layout .el-table--striped .el-table__body tr:nth-child(2n) td{background:#F9FAFC
}
.admin-layout input::-webkit-input-placeholder{color:#A5A5A5 !important;font-weight:lighter
}
.admin-layout input:-moz-placeholder{color:#A5A5A5 !important;font-weight:lighter
}
.admin-layout input:-ms-input-placeholder{color:#A5A5A5 !important;font-weight:lighter
}
.admin-layout .el-select .el-input .el-input__icon{color:#D3D1CD !important;font-weight:lighter
}
.admin-layout .el-input.is-disabled .el-input__inner{border-color:#B8B8B8;background-color:#F9F9F9
}
.messageDialog,.el-message-box__wrapper,.reset_dialog,.message-detail-dialog,.editor-group-dialog,.join-group-dialog,.weight-chart-dialog{background:rgba(0,0,0,0.5)
}
.common-loading{background-color:rgba(255,255,255,0.7) !important
}


