
.doctor-message-list div.el-tabs__header {
  border-bottom: 0;
}
.doctor-message-list div.el-tabs__header div.el-tabs__active-bar {
  top: 0;
  bottom: auto;
}
.doctor-message-list div.el-tabs__header div.el-tabs__item {
  font-size: 18px;
  height: 50px;
  line-height: 50px;
  padding: 0;
  text-align: center;
  border-bottom: 1px solid #e7e7e7;
  border-top: 1px solid #e7e7e7;
  background-color: #f8f8f8;
  color: #000;
}
.doctor-message-list div.el-tabs__header div.el-tabs__item.is-active {
  border-bottom: 0;
  background-color: transparent;
  color: #20a0ff;
}
.doctor-message-list div.el-tabs__header div.el-tabs__item .el-badge__content.is-fixed {
  top: 18px;
}
.doctor-message-list div.el-tabs__header div.el-tabs__item .el-badge__content.is-fixed.is-dot {
  right: 0;
}
.doctor-message-list .message-detail-dialog .el-dialog__body {
  padding: 0 20px 20px;
}
.doctor-message-list .message-detail-dialog .el-dialog--tiny {
  width: 520px;
  border-radius: 10px;
}
.doctor-message-list .message-detail-dialog .el-dialog__header {
  padding: 15px 0;
  margin: 0 20px;
  border-bottom: 1px solid #ebe9e9;
}
.doctor-message-list .message-detail-dialog .el-dialog__header .el-dialog__title {
  font-weight: normal;
  color: #000;
  font-size: 17px;
}
.doctor-message-list .message-detail-dialog .el-dialog__header .el-dialog__close {
  font-size: 14px;
}
.doctor-message-list .message-detail-dialog .dialog-footer2 {
  text-align: center;
}
.doctor-message-list .message-detail-dialog .list {
  padding-bottom: 30px;
  margin-bottom: 30px;
  border-bottom: 1px solid #ebe9e9;
  line-height: 20px;
  font-size: 13px;
  font-weight: normal;
}
.doctor-message-list .message-detail-dialog .list .item {
  margin-top: 30px;
  overflow: hidden;
  color: #000;
}
.doctor-message-list .message-detail-dialog .list .item .label {
  float: left;
  width: 130px;
  text-align: right;
}
.doctor-message-list .message-detail-dialog .list .item .detail {
  float: left;
  width: 290px;
  margin-left: 15px;
}


