
.message{text-align:left;white-space:normal;word-break:break-all
}
.message .img_box{height:200px;margin:0 -20px
}
.message .img_box img{height:200px;width:auto;max-width:100%;border-radius:4px
}
.message .sound_box .audio,.message .sound_box .audioPlay{display:inline-block;width:60px;background-repeat:no-repeat;background-size:30px;cursor:pointer
}
.right .msgBox .message .sound_box .audio,.right .msgBox .message .sound_box .audioPlay{text-align:left;background-position:center right
}
.right .msgBox .message .sound_box .audio{background-image:url("../../../static/msgVoice.svg")
}
.right .msgBox .message .sound_box .audioPlay{background-image:url("../../../static/icon-message-voice-doctor.gif")
}
.left .msgBox .message .sound_box .audio,.left .msgBox .message .sound_box .audioPlay{text-align:right;background-position:center left
}
.left .msgBox .message .sound_box .audio{background-image:url("../../../static/msgVoiceDef.svg")
}
.left .msgBox .message .sound_box .audioPlay{background-image:url("../../../static/icon-message-voice-patient.gif")
}


