
.weight-chart-dialog .el-dialog {
  width: 950px;
  border-radius: 5px;
}
.weight-chart-dialog .el-dialog .el-dialog__body {
  padding: 0 0 30px;
}
.weight-chart-dialog .el-dialog .el-dialog__body .title {
  font-size: 18px;
  color: #000;
  padding-bottom: 20px;
  border-bottom: 1px solid #e7e7e7;
  margin: 0 20px 30px;
  font-weight: normal;
}
.weight-chart-dialog .el-dialog .el-dialog__body .title span {
  padding-left: 20px;
}
.add-weight-dialog .el-dialog {
  width: 400px;
}
.add-weight-dialog .el-dialog .el-dialog__title {
  font-size: 18px;
  color: #000;
}
.add-weight-dialog .el-dialog .el-dialog__close {
  font-size: 14px;
  color: #a5a5a5;
}
.add-weight-dialog .el-dialog .el-dialog__body {
  border-top: 1px solid #e7e7e7;
  margin: 15px 20px 0;
  padding: 0;
}
.add-weight-dialog .el-dialog .el-form-item {
  margin-top: 30px;
  color: #000;
}
.add-weight-dialog .el-dialog .el-form-item .el-form-item__label {
  padding-right: 15px;
  text-align: right;
  color: #000;
}
.add-weight-dialog .el-dialog .el-form-item .el-from-input,
.add-weight-dialog .el-dialog .el-form-item .el-form-time {
  width: 180px;
}
.add-weight-dialog .el-dialog .el-form-item.is-required .el-form-item__label:before {
  display: none;
}
.add-weight-dialog .el-dialog .dialog-footer2 {
  margin: 30px 0 0;
  padding: 20px;
  border-top: 1px solid #e7e7e7;
  text-align: center;
}
.add-weight-dialog .el-dialog .dialog-footer2 .el-button {
  width: 88px;
}
.add-weight-dialog .el-dialog .dialog-footer2 .el-button+.el-button {
  margin-left: 30px;
}
.add-weight-dialog .el-dialog .tips {
  position: absolute;
  right: 60px;
  bottom: 0;
}
.weight-table .el-table__body tr {
  cursor: pointer;
}


