
.admin-layout .reset_dialog .el-dialog__header,
.user-layout .reset_dialog .el-dialog__header {
  padding: 0;
  height: 50px;
  line-height: 50px;
  position: relative;
  border-bottom: 1px solid #e7e7e7;
  margin: 0 20px;
}
.admin-layout .reset_dialog .el-dialog__body,
.user-layout .reset_dialog .el-dialog__body {
  padding: 30px 20px 20px;
}
.admin-layout .reset_dialog .el-dialog--tiny,
.user-layout .reset_dialog .el-dialog--tiny {
  width: 520px;
  border-radius: 4px;
}
.admin-layout .reset_dialog .el-dialog__title,
.user-layout .reset_dialog .el-dialog__title {
  font-size: 18px;
  color: #000;
  font-weight: normal;
}
.admin-layout .reset_dialog .el-dialog__headerbtn,
.user-layout .reset_dialog .el-dialog__headerbtn {
  position: absolute;
  right: 0;
  top: 0;
  font-size: 14px;
}
.admin-layout .reset_dialog .list,
.user-layout .reset_dialog .list {
  position: relative;
}
.admin-layout .reset_dialog .item,
.user-layout .reset_dialog .item {
  list-style: none;
  margin-bottom: 30px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
  justify-content: center;
  -webkit-align-items: center;
  align-items: center;
}
.admin-layout .reset_dialog .item:last-child,
.user-layout .reset_dialog .item:last-child {
  border-top: 1px solid #e7e7e7;
  padding-top: 20px;
  margin-bottom: 0;
}
.admin-layout .reset_dialog .item:last-child .el-button,
.user-layout .reset_dialog .item:last-child .el-button {
  width: 88px;
}
.admin-layout .reset_dialog .label_name,
.user-layout .reset_dialog .label_name {
  color: #000;
  margin-right: 12px;
  font-weight: normal;
}
.admin-layout .reset_dialog .el-input,
.user-layout .reset_dialog .el-input {
  width: 238px;
}
.admin-layout .reset_dialog .el-button+.el-button,
.user-layout .reset_dialog .el-button+.el-button {
  margin-left: 20px;
}
.admin-layout .reset_dialog .error_tips,
.user-layout .reset_dialog .error_tips {
  position: absolute;
  left: 164px;
  bottom: 48px;
  color: #ff4d4d;
  font-weight: normal;
}
.admin-layout .reset_dialog .input_error .el-input__inner,
.user-layout .reset_dialog .input_error .el-input__inner {
  border-color: #ff4949 !important;
  box-shadow: 0 0 0 1px #ff4949 inset;
}
.admin-layout .reset_dialog_success .list,
.user-layout .reset_dialog_success .list {
  padding-top: 200px;
}
.admin-layout .reset_dialog_success .tips,
.user-layout .reset_dialog_success .tips {
  position: absolute;
  top: 60px;
  width: 100%;
  left: 0;
  text-align: center;
}
.admin-layout .reset_dialog_success .tips .con,
.user-layout .reset_dialog_success .tips .con {
  display: inline-block;
  line-height: 40px;
  padding-left: 60px;
  background: url("../../../../static/header/icon-success.svg") no-repeat 0 center/auto 100%;
  color: #000;
}
.admin-layout .message .item:hover *,
.admin-layout .tool .item:hover * {
  fill: #40aaff;
}


