
.customer-bp-g .el-dialog {
  width: inherit;
  padding-left: 20px;
  padding-right: 20px;
  border-radius: 5px;
  min-width: 612px;
}
.customer-bp-g .el-dialog__header {
  border-bottom: 1px solid #e0e6ed;
  padding: 40px 0 10px 0;
}
.customer-bp-g .el-dialog__header .el-dialog__title {
  font-weight: normal;
}
.customer-bp-g .el-dialog__body {
  padding-top: 0;
}
.customer-bp-g .el-dialog__footer {
  text-align: center;
}
.customer-bp-g .form-line-title {
  display: inline-block;
  min-width: 63px;
}
.customer-bp-g .form-line {
  margin-top: 30px;
}
.customer-bp-g .el-input-group__append {
  background: transparent;
  border-left: none;
}
.customer-bp-g .el-input-group--append input {
  border-right: none;
}
.customer-bp-g .bp-hd .el-tabs__item {
  height: inherit;
  line-height: inherit;
  padding: 20px 10px;
}
.customer-bp-g .bp-hd .el-tabs__header {
  margin: 0;
}


