
.messageDialog .tips {
  position: absolute;
  right: 100px;
  bottom: 10px;
  color: #a5a5a5;
  font-size: 12px;
}
.messageDialog .el-dialog__body {
  padding: 0;
  color: #000;
}
.messageDialog .el-dialog--tiny {
  width: 750px;
  border-radius: 4px;
}
.messageDialog .el-dialog__header {
  position: relative;
  padding: 0;
  margin: 0 20px;
  height: 50px;
  line-height: 50px;
  border-bottom: 1px solid #c9d2dc;
}
.messageDialog .el-dialog__title {
  font-size: 18px;
  font-weight: normal;
  color: #000;
}
.messageDialog .el-dialog__headerbtn {
  position: absolute;
  top: 0;
  right: 0;
  font-size: 14px;
}
.messageDialog .form_detail {
  padding-top: 10px;
}
.messageDialog .form_detail .form_item {
  margin-top: 30px;
  position: relative;
}
.messageDialog .form_detail .form_item:after {
  content: "";
  clear: both;
  display: block;
  height: 0;
}
.messageDialog .form_detail .form_item .label {
  float: left;
  width: 150px;
  text-align: right;
  font-size: 14px;
  line-height: 36px;
  margin-right: 10px;
  color: #000;
}
.messageDialog .form_detail .form_item .detail {
  float: left;
  width: 500px;
}
.messageDialog .form_detail .form_item .send_users {
  border: 1px solid #c9d2dc;
  border-radius: 5px;
  padding: 0 5px 5px;
  overflow: hidden;
  min-height: 30px;
  width: 488px;
}
.messageDialog .form_detail .form_item .send_users .item {
  float: left;
  margin: 5px 5px 0 0;
  line-height: 23px;
  padding: 0 25px 0 5px;
  border: 1px solid #cbe6ff;
  background: #edf6ff;
  position: relative;
  color: #4cb0ff;
  font-size: 12px;
}
.messageDialog .form_detail .form_item .send_users .item .close {
  position: absolute;
  right: 2px;
  top: 3px;
  cursor: pointer;
  font-size: 16px;
  font-family: cursive;
  transform: rotate(-2deg);
  font-family: element-icons !important;
  width: 18px;
  height: 18px;
  line-height: 18px;
  font-size: 12px;
  transform: scale(0.75);
  text-align: center;
  border-radius: 50%;
}
.messageDialog .form_detail .form_item .send_users .item .close:before {
  content: "\E60C";
}
.messageDialog .form_detail .form_item .send_users .item .close:hover {
  background: #20a0ff;
  color: #fff;
}
.messageDialog .form_detail .form_item .textarea {
  width: 500px;
  font-size: 14px;
  color: #000;
}
.messageDialog .form_detail .form_item .textarea_error .el-textarea__inner,
.messageDialog .form_detail .form_item .textarea_error .el-input__inner {
  border-color: #ff4d4d;
}
.messageDialog .form_detail .form_item .textarea_error_tips {
  position: absolute;
  left: 160px;
  bottom: -25px;
  color: #ff4d4d;
}
.messageDialog .form_detail .form_footer {
  text-align: center;
  margin: 30px 20px 0;
  border-top: 1px solid #c9d2dc;
  padding: 20px 0 20px;
}
.messageDialog .form_detail .form_footer .el-button {
  width: 88px;
}
.messageDialog .form_detail .form_footer .el-button+.el-button {
  margin-left: 30px;
}
.messageDialog .log_search {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
  align-items: center;
  padding: 20px;
  overflow: hidden;
  position: relative;
}
.messageDialog .log_search .serach_input {
  width: 160px;
  margin: 0 40px 0 10px;
  display: inline-block;
  vertical-align: middle;
}
.messageDialog .log_search .search_time {
  width: 220px;
  margin: 0 0 0 10px;
  display: inline-block;
  vertical-align: middle;
}
.messageDialog .log_search .search_btn {
  position: absolute;
  right: 0;
  top: 20px;
  width: 36px;
  height: 36px;
  background: #40aaff url("../../../../../../static/customer/icon-search.svg") no-repeat 0 0/100%;
  border-radius: 5px;
}
.messageDialog .page_con {
  text-align: right;
  padding: 20px 0 0;
}
.messageDialog .message_detail {
  width: 500px;
  margin: 0 auto;
  padding: 60px 0 0;
}
.messageDialog .message_detail td {
  padding: 15px 10px;
}
.el-message-box .el-message-box__content {
  color: #000;
}
.group-message-box {
  width: 416px;
  border-radius: 10px;
}
.group-message-box .el-message-box__header {
  padding: 40px 0 0 80px;
  color: #000;
  font-size: 18px;
}
.group-message-box .el-message-box__close {
  display: none;
}
.group-message-box .el-message-box__content {
  padding-top: 20px;
}
.group-message-box .el-message-box__status {
  top: -6px;
  left: 40px;
  font-size: 24px !important;
}
.group-message-box .el-message-box__message {
  padding: 0 40px 0 10px;
}
.group-message-box .el-message-box__btns {
  padding: 0 40px 30px 0;
}
.group-message-box .el-message-box__btns button:nth-child(2) {
  margin-left: 30px;
}


