
.customer-bp-graph {
  margin-top: 40px;
}
.customer-bp-graph .el-button {
  width: 74px;
  height: 36px;
}
.customer-bp-graph h4 {
  font-weight: 200;
  margin-bottom: 40px;
}
.customer-bp-graph [m-class="active"] {
  background: #20a0ff;
  color: #fff;
  border: none;
}
.customer-bp-graph .el-button:focus {
  background: #20a0ff;
  color: #fff;
}
.customer-bp-graph .x-axis path,
.customer-bp-graph .y-axis path,
.customer-bp-graph .x-axis line,
.customer-bp-graph .y-axis line {
  stroke: #e0e6ed;
  stroke-width: 0.5px;
}
.customer-bp-graph text {
  font-family: Microsoft YaHei, "Helvetica Neue", Helvetica, Arial, sans-serif;
}
.customer-bp-graph .sys-line,
.customer-bp-graph .dia-line {
  stroke-width: 2px;
}
.customer-bp-graph .sys-line {
  fill: none;
  stroke: #20a0ff;
}
.customer-bp-graph .dia-line {
  fill: none;
  stroke: #35bf32;
}
.customer-bp-graph .dashed {
  stroke-dasharray: 6, 6;
}
.customer-bp-graph .point-inner {
  fill: #fff;
}
.customer-bp-graph .dia-point-outer {
  fill: #35bf32;
}
.customer-bp-graph .sys-point-outer {
  fill: #20a0ff;
}
.customer-bp-graph .text-shift text {
  transform: translateX(15px);
}
.customer-bp-graph .sys-bar {
  fill: #20a0ff;
}
.customer-bp-graph .dia-bar {
  fill: #35bf32;
}
.customer-bp-graph .axis-text {
  font-size: 14px;
}
.customer-bp-graph .x-axis text,
.customer-bp-graph .y-axis text {
  font-size: 14px;
}
.customer-bp-graph .mark-line {
  stroke: #99a9bf;
  stroke-width: 2px;
}
.customer-bp-graph .svg-wrapper {
  height: 370px;
  margin: auto;
  position: relative;
}
.customer-bp-graph .svg-wrapper .bp-graph {
  display: block;
  margin: auto;
}
.customer-bp-graph .graph-toggles {
  margin-top: 60px;
  text-align: center;
}
.customer-bp-graph .mark-tip-box {
  width: 120px;
  height: 74px;
  border-radius: 8px;
  background: #1f2d3d;
  opacity: 0.85;
  color: #fff;
  font-size: 12px;
  z-index: 101;
  position: absolute;
  padding-top: 8px;
  padding-left: 8px;
  line-height: 20px;
  transform: translateX(-50%);
}
.customer-bp-graph .mark-sample {
  margin-left: 45px;
}
.customer-bp-graph .err-count-mark-sample-dia,
.customer-bp-graph .err-count-mark-sample-sys {
  border-radius: 2px;
  display: inline-block;
  vertical-align: middle;
  height: 20px;
  width: 20px;
  background-color: #20a0ff;
  margin-right: 10px;
}
.customer-bp-graph .err-count-mark-sample-dia {
  margin-left: 80px;
  background-color: #35bf32;
}
.no-data-tip {
  color: #666;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
.svg-scroller {
  width: 100%;
  height: 467px;
  overflow-x: scroll;
  overflow-y: visible;
}
.trend-dia-mark,
.trend-sys-mark {
  display: inline-block;
  height: 12px;
  width: 30px;
  vertical-align: middle;
}
.trend-dia-mark {
  margin-left: 30px;
  background: url("./assets/icon-Explain-dia.svg");
}
.trend-sys-mark {
  background: url("./assets/icon-Explain-sys.svg");
}


